<template>
  <div>
    <h1>내 캐릭터 등록하기</h1>
    <h3>파티를 구하고 있다는 내용을 등록합니다.</h3>
    <div id="character">
        <div><label for="world">서버 <select id="world" v-model="world">
            <option value="">선택</option>
            <option value="krChocobo">초코보</option>
            <option value="krMoogle">모그리</option>
            <option value="krCarbuncle">카벙클</option>
            <option value="krTonberry">톤베리</option>
        </select></label></div>
        <div><label for="nickname">캐릭터 명 <input type="text" name="nickname" id="nickname" placeholder="캐릭터 명" v-model="nickname" maxlength="9" autocomplete="off" required></label></div>
    </div>
    <div id="raid">
        <p>■ 참여를 원하는 레이드</p>
        <div><label for="raids"><select id="raids" v-model="raid">
            <option value="">선택</option>
            <option value="sepulture">각성 영식</option>
            <option value="tea">절 알렉</option>
            <option value="etc">기타</option>
        </select></label></div>
    </div>
    <div id="job-main" class="job">
        <p>■ 주 직업</p>
        <div :id="'job-' + key" v-for="(jobs, key) in jobList" :key="key">
            <div>
                <div v-for="job in jobs" :key="job.engName" :class="{ checked: job.engName == mainJob }">
                    <label><input type="radio" :value="job.engName" :class="job.engName" name="job-main" v-on:click="mainJob = $event.target.value">{{ job.korName }}</label>
                </div>
            </div>
        </div>
    </div>
    <div id="job-sub" class="job">
        <p>■ 참여 가능한 잡 (선택 안해도 무방)</p>
        <div :id="'job-' + key" v-for="(jobs, key) in jobList" :key="key">
            <div>
                <div v-for="job in jobs" :key="job.engName" :class="{ checked: job.checked, mainJob: job.engName == mainJob }">
                    <label><input type="checkbox" :value="job.engName" :class="job.engName" v-model="job.checked">{{ job.korName }}</label>
                </div>
            </div>
        </div>
    </div>
    <div id="agreement">
        <p>■ 캐릭터 정보 표시 동의</p>
        등록시 해당 캐릭터 닉네임의 사사게 게시글 제목 언급 횟수를 표시함에 동의합니다. 
        <a :href="'http://www.inven.co.kr/board/ff14/4485?name=subject&keyword='+nickname" target="_blank" v-on:click="openSasagae($event)">사사게 확인하기</a>
        <label class="agreement"><input type="checkbox" name="agreeSasagae">필수</label><br>
        등록시 해당 캐릭터의 FFLog 기록을 표시함에 동의합니다. 
        <a :href="makeFFLogLink()" target="_blank" v-on:click="openFFLog($event)">로그 확인하기</a>
        <label class="agreement"><input type="checkbox" name="agreeFFLog">필수</label>
    </div>
    <div id="etc">
        <p>■ 기타 전달하고 싶은 내용 (최대 200자)</p>
        <h5>사사게에 본인 닉네임이 등록된 특이한 사유가 있다거나<br>
        참여 가능한 잡들의 숙련도 정도같은 사항을 작성해주세요.</h5>
        <textarea name="etc" cols="46" rows="10"></textarea>
    </div>
    <div id="contact">
        <p>■ 연락처 (최소 1개 선택)</p>
        <div class="openkakao">
          <input type="checkbox" id="openkakao" v-on:click="enableInput($event.target)">
          <label for="openkakao">오픈 카톡 링크 </label>
          <input type="text" id="openkakaoLink" autocomplete="off" v-model="openkakaoLink" disabled>
        </div>
    </div>
    <div id="password">
        <p>■ 게시글 수정 / 삭제를 위한 비밀번호 설정 (4-15자)</p>
        <h5>가능하면 다른 웹사이트에서 사용하지 않는 비밀번호를 입력해주세요.</h5>
        <div><label for="passwordInput">비밀번호</label> <input type="password" name="passwordInput" id="passwordInput" placeholder="비밀번호" maxlength="15" required></div>
        <div><label for="passwordCheck">비밀번호 확인</label> <input type="password" name="passwordCheck" id="passwordCheck" placeholder="비밀번호" maxlength="15" required></div>
    </div>
    <div id="serviceAgreement">
        <p>■ 서비스 이용 동의</p>
        익명성을 이용한 기능 악용시 IP밴을 위한 IP수집에 동의합니다. <label class="agreement"><input type="checkbox" name="agreeIP">필수</label><br>
    </div>
    <div id="register">
      <div id="registerMessage"></div><br>
      <div v-on:click="register()" id="registerButton">등록하기</div>
    </div>
  </div>
</template>

<script>
// TODO: url에 unicode 인코딩하기
import { mapState } from 'vuex';
export default {
  name: 'newMember',
  data () {
    return {
      world: '',
      raid: '',
      nickname: '',
      mainJob: null,
      openkakaoLink: '',
      jobList: {
        tank: [
            { korName: '나이트', engName: 'pld', checked: false },
            { korName: '전사', engName: 'war', checked: false },
            { korName: '암흑기사', engName: 'drk', checked: false },
            { korName: '건브레이커', engName: 'gnb', checked: false },
        ],
        healer: [
            { korName: '백마도사', engName: 'whm', checked: false },
            { korName: '학자', engName: 'sch', checked: false },
            { korName: '점성술사', engName: 'ast', checked: false },
        ],
        melee: [
            { korName: '몽크', engName: 'mnk', checked: false },
            { korName: '용기사', engName: 'drg', checked: false },
            { korName: '닌자', engName: 'nin', checked: false },
            { korName: '사무라이', engName: 'sam', checked: false },
        ],
        ranged: [
            { korName: '음유시인', engName: 'brd', checked: false },
            { korName: '기공사', engName: 'mch', checked: false },
            { korName: '무도가', engName: 'dnc', checked: false },
        ],
        caster: [
            { korName: '흑마도사', engName: 'blm', checked: false },
            { korName: '소환사', engName: 'smn', checked: false },
            { korName: '적마도사', engName: 'rdm', checked: false },
        ]
      },
      animationTimeout: null,
    }
  },
  computed: {
    ...mapState({
      count: state => state.count,
    })
  },
  methods: {
    openSasagae(event){
      event.preventDefault();
      const nicknameRegex = /[가-힣|a-z|A-Z|']{2,9}/;
      if (this.nickname.match(nicknameRegex) == null) {
        alert('올바른 캐릭터 명 입력 후 확인 가능합니다.');
        return;
      }
      window.open(event.target.href);
    },
    makeFFLogLink(){
      let world = this.world.substr(2);
      const url = `https://ko.fflogs.com/character/kr/${world}/${this.nickname}`;
      return url;
    },
    openFFLog(event){
      event.preventDefault();
      const nicknameRegex = /[가-힣|a-z|A-Z|']{2,9}/;
      if (this.world == '' || this.nickname.match(nicknameRegex) == null) {
        alert('서버를 선택하고 올바른 캐릭터 명 입력 후 확인 가능합니다.');
        return;
      }
      window.open(event.target.href);
    },
    enableInput(target){
      document.querySelector(`#${target.id} + label + input`).disabled = !target.checked;
    },
    register(){
      let alertMessage = '';
      const nicknameRegex = /[가-힣|a-z|A-Z|']{2,9}/;
      const openkakaoRegex = /^(https:\/\/)*open.kakao.com\/o\//;
      if (this.world == '') alertMessage += '서버를 선택하세요.<br>';
      if (this.nickname.match(nicknameRegex) == null) alertMessage += '올바른 캐릭터 명을 입력하세요.<br>';
      if (this.raid == '') alertMessage += '레이드를 선택하세요.<br>';
      if(this.mainJob == null) alertMessage += '주 직업을 선택하세요.<br>';
      if (!document.querySelector('[name=agreeSasagae]').checked) alertMessage += '사사게 언급 횟수 표시에 동의하지 않으면 등록할 수 없습니다.<br>';
      if (!document.querySelector('[name=agreeFFLog]').checked) alertMessage += 'FFLog 표시에 동의하지 않으면 등록할 수 없습니다.<br>';
      if (document.querySelector('input#openkakao').checked && !document.querySelector('#openkakaoLink').value.match(openkakaoRegex)) alertMessage += '올바른 오픈카톡 링크를 입력하세요.<br>';
      if (!document.querySelector('#passwordInput').value) alertMessage += '비밀번호를 입력하세요.<br>';
      else if (document.querySelector('#passwordInput').value.length < 4) alertMessage += '비밀번호 길이를 확인하세요.<br>';
      if (document.querySelector('#passwordInput').value != document.querySelector('#passwordCheck').value) alertMessage += '비밀번호와 비밀번호 확인이 다릅니다.<br>';
      if (!document.querySelector('[name=agreeIP]').checked) alertMessage += 'IP 수집에 동의하지 않으면 등록할 수 없습니다.<br>';
      if (alertMessage != '') {
        document.querySelector('#registerMessage').innerHTML = alertMessage;

        clearTimeout(this.animationTimeout);
        document.querySelector('#registerMessage').classList.remove('alertRed');
        document.querySelector('#registerMessage').classList.add('alertRed');
        this.animationTimeout = setTimeout(()=> {
          document.querySelector('#registerMessage').classList.remove('alertRed');
        }, 800);
        return;
      }
      document.querySelector('#registerMessage').innerHTML = '';
      alert('이제 서버로 전송할 차례 (미구현)');
    },
  }
}
</script>

<style scoped>
h1, h2, h3 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
div#character {
  margin: 10px;
}
div#character > div {
  display: inline-block;
  text-align: center;
}
div.job > p {
  margin: 15px 0 10px 0;
}
div.job > div > div > div {
  display: inline-block;
}
div.job > div > div {
  width: 350px;
  display: inline-block;
  color: white;
  user-select: none;
}
div.job > div#job-tank > div {
  background-color: rgb(46, 76, 222);
}
div.job > div#job-healer > div {
  background-color: rgb(41, 142, 9);
}
div.job > div#job-melee > div,
div.job > div#job-ranged > div,
div.job > div#job-caster > div {
  background-color: rgb(171, 57, 66);
}
div.job div.checked {
  opacity: .5;
}
div#job-sub div.mainJob {
  display: none;
}
label.agreement {
  user-select: none;
}
div#contact > div > label {
  user-select: none;
}
div#password > div > label {
  display: inline-block;
  width: 110px;
  text-align: left;
}
div#registerMessage {
  margin: 5px;
  padding: 5px;
  display: inline-block;
  color: red;
}
div.alertRed {
  animation-duration: .8s;
  animation-name: alertRed;
}
div#register > div#registerButton {
  display: inline-block;
  padding: 5px;
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgb(233, 233, 233);
  user-select: none;
}

@keyframes alertRed {
  from {
    background-color: rgb(255, 203, 136);
  }

  to {
    background-color: none;
  }
}
</style>



